@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@300;400;700&display=swap);
.about__container {
  background-image: url(/react-exam/static/media/couple.60797df0.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  height: 90vh;
  text-align: center;
  margin-top: -1.5em;
  display: flex;
  flex-direction: column;
  justify-content: center;
  place-items: center;
  font-size: 1.5rem;
  text-align: center;
}
.paragraph__container {
  margin: 2em;
}

.mail__to {
  width: 1em;
}
.tmdb__link {
  color: var(--clr-accent);
  text-decoration: none;
}
.copyright {
  text-align: right;
}

.btn {
  font-size: 1.6rem;
  background-color: var(--clr-primary);
  color: var(--clr-accent);
  padding: 0.5em;
  border: solid 1px var(--clr-accent);
  border-radius: 15px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}
.btn:hover {
  background-color: var(--clr-secondary);
  cursor: pointer;
}

.movie__container {
  border: solid 4px var(--clr-accent);
  background-color: var(--clr-secondary-opacity);
  border-radius: 10px;
  width: auto;
  margin: auto;
  margin-top: 3em;
  margin-bottom: 2em;
  box-shadow: 5px 5px 5px #212121;
}

.home__container {
  text-align: center;
  margin: 0;

  height: 100vh;
}
.search__container {
  background-size: 50%;
  background-position: center;
  background-repeat: no-repeat;
  display: flex;
  flex-direction: column;
  place-items: center;
  align-content: center;

  margin-bottom: 3em;
}
.title {
  background-image: url(/react-exam/static/media/woman_eating.c377c3fb.jpg);
  background-size: cover;
  box-shadow: 0 0 30px 30px black inset;
  display: flex;
  justify-content: center;
  color: var(--clr-accent);
  width: 45em;
  height: 30em;
  padding: 0.5em;
  margin: 1em;
}

.title h1 {
  text-align: right;
  font-size: 2.5rem;
  text-shadow: 3px 3px var(--clr-primary);
}

.card__container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  grid-gap: 3em;
  gap: 3em;
}
.input {
  height: 2rem;
  width: 20rem;
  font-size: 1.3rem;
  margin: 2em;
  border-radius: 5px;
  border: none;
  padding: 0.8em;
}
.result__container {
  background-image: url(/react-exam/static/media/chairs.81741049.jpg);
  background-size: cover;
  box-shadow: 0 0 15px 15px black inset;
  background-position: center;
  min-height: 40vh;
}
.result__header {
  font-size: 1.8rem;
  font-weight: 700;
}
.movie__poster:hover {
  cursor: pointer;
}

@media screen and (max-width: 700px) {
  .result__container {
    background-image: none;
  }

  .title {
    background-image: none;
    width: 20em;
    height: 12em;
  }

  .title h1 {
    text-align: center;
    font-size: 2rem;
  }
  .input {
    width: 16rem;
  }
  .card__container {
    flex-direction: column;
    align-content: center;
    grid-gap: 1em;
    gap: 1em;
  }
}

.poster {
  border-radius: 8px;
  box-shadow: 5px 5px 5px #212121;

  margin: auto;
}
.card {
  width: -webkit-min-content;
  width: min-content;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}

.four_O_four {
  background-image: url(/react-exam/static/media/ofGravity.f70295b5.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  text-align: center;
  height: 100vh;
  padding: 5em;
  margin-top: -3em;
}

.movieView__container {
  background-image: url(/react-exam/static/media/popcorn.972bff64.jpg);
  background-size: cover;
  background-position: right bottom;
  text-align: center;
  padding: 1em;
}

.movie__container {
  border: solid 4px var(--clr-accent);
  background-color: var(--clr-secondary-opacity);
  border-radius: 10px;
  width: 55vw;
  margin: auto;
  margin-top: 3em;
  margin-bottom: 2em;
}

.movie__backdrop {
  width: 80%;
  margin-left: auto;
  margin-right: auto;
  border-radius: 10px;
  box-shadow: 8px 8px 8px #212121;
}

.movie__data {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: 1fr, 2fr, 2fr;
  grid-gap: 2em;
  gap: 2em;
}

.movie__title {
  text-align: center;
}
.movie__card {
  grid-column: 2;
  grid-row: 4;
  text-align: center;
  justify-self: end;
}
.overview {
  grid-column: 3;
  grid-row: 4;
  align-self: center;
  justify-self: center;
  width: 25vw;
  font-size: 1.3rem;
  text-align: left;
}
.release__date {
  grid-column-start: 2;
  grid-row: 3;
  justify-self: center;
  font-size: 1.5rem;
}

.voting {
  grid-column-start: 3;
  grid-row: 3;
  text-align: center;
  font-size: 1.5rem;
}

@media screen and (max-width: 950px) {
  .movie__data {
    display: flex;
    flex-direction: column;
    grid-gap: 0.2em;
    gap: 0.2em;
    place-items: center;
    margin-top: 0.2em;
  }
  .overview {
    width: 90%;
    font-size: 1.2rem;
    text-align: center;
  }
  .voting {
    order: -1;
  }
  .release__date {
    order: -1;
  }
  .movie__container {
    width: 80vw;
  }
}

.video-responsive {
  overflow: hidden;
  padding-bottom: 3em;
  position: relative;
}
.video-responsive iframe {
  height: 50vh;
  width: 45vw;
}

.signin__container {
  display: flex;
  flex-direction: column;
  width: 20em;
  margin: 10em;
}

.favourite__container {
  background-image: url(/react-exam/static/media/popcorn.972bff64.jpg);
  background-size: cover;
  background-position: right bottom;
  box-shadow: 0 0 30px 30px black inset;
  text-align: center;
  padding: 1em;
  height: 100vh;
}

.favourites {
  display: grid;
  grid-template-columns: 1fr 1fr;
  text-align: center;
}

@media screen and (max-width: 950px) {
  .favourites {
    display: flex;
    flex-direction: column;
  }
}

.navDesk__container {
  position: relative;
  background: linear-gradient(
    to left,
    var(--clr-primary),
    var(--clr-primary-dark)
  );

  height: 8em;
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  place-items: center;
  align-content: center;
}
.navDesk__container button {
  width: 5em;
  margin-right: 0.5em;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.nav__btns {
  grid-column: 10;
  display: flex;
  flex-direction: row;
  list-style: none;
  margin-right: 2em;
  align-items: center;
}
.nav__btn--home {
  grid-column: 7;
}
.nav__btn--about {
  grid-column-start: 10;
}
.nav__btn--signin {
  grid-column-start: 10;
}
.logo {
  grid-column-start: 1;
  margin-left: 1.5em;
  width: 8em;
  cursor: pointer;
}
.logo:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

@media screen and (max-width: 700px) {
  .nav__btns {
    display: none;
  }
  .hamburger {
    position: absolute;
    border: solid 1.2px black;
    background-color: white;
    right: 1em;
    height: 2em;
    width: 2em;
    display: grid;
    place-items: center;
    align-content: space-around;
    border-radius: 0.3em;
  }
  .hamburger:hover {
    cursor: pointer;
  }
  .hamburger__line {
    height: 0.2em;
    width: 1.2em;
    background-color: black;
  }
  .hamburger__line1,
  .hamburger__line2,
  .hamburger__line3 {
    transition: 400ms;
  }

  .hamburger--crossed .hamburger__line1 {
    -webkit-transform: translateY(0.6rem) rotate(45deg);
            transform: translateY(0.6rem) rotate(45deg);
  }
  .hamburger--crossed .hamburger__line2 {
    -webkit-transform: translateY(1.4em);
            transform: translateY(1.4em);
    opacity: 0;
  }
  .hamburger--crossed .hamburger__line3 {
    -webkit-transform: translateY(-0.6rem) rotate(-45deg);
            transform: translateY(-0.6rem) rotate(-45deg);
  }
}

.profile {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 8em;
  grid-gap: 1em;
  gap: 1em;
}
.profile:hover {
  cursor: pointer;
}
.profile__username {
  color: var(--clr-accent);
}
.profile__img {
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
}
@media screen and (max-width: 700px) {
  .profile {
    flex-direction: column;
    grid-gap: 0.2em;
    gap: 0.2em;
  }
  .profile__img {
    margin-top: 0.2em;
  }
}

.dropdown__container {
  position: absolute;
  top: 100%;
  right: 0;
  background-color: var(--clr-primary-dark);
  z-index: 999;
  color: var(--clr-accent);
  text-align: center;
  font-size: 1.2rem;
  display: none;
  width: 10rem;
}
.profile:focus-within .dropdown__container,
.profile:hover .dropdown__container {
  display: block;
}
.dropdown__container button {
  font-size: 1.2rem;
  background-color: var(--clr-primary-dark);
  color: var(--clr-accent);
  border: none;
  width: 100%;
  margin-bottom: 0.5em;
}
.dropdown__container button:focus,
.dropdown__container button:hover {
  cursor: pointer;
}

.navigationDropdown__container {
  display: none;
}

@media screen and (max-width: 700px) {
  .navigationDropdown__container {
    display: block;
    position: absolute;
    right: 0;
    top: 100%;
    width: 10rem;
    list-style: none;
    background-color: var(--clr-primary-dark);
    color: var(--clr-accent);
  }
  .navigationDropdown__container button {
    background-color: var(--clr-primary-dark);
    color: var(--clr-accent);
    border: none;
    border-radius: 0;
    padding: 0.8em;
    font-size: 1.2rem;
    width: 100%;
    margin: 0;
  }
  .navigationDropdown__container button:hover,
  .navigationDropdown__container button:focus {
    cursor: pointer;
    background-color: var(--clr-primary);
  }
}

:root {
  --clr-primary: #852024;
  --clr-primary-dark: #4d0e10;
  --clr-secondary: #103c61;
  --clr-secondary-opacity: #103c61b0;
  --clr-secondary-dark-opacity: #0c243894;
  --clr-accent: #cfb584;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Source Sans Pro", sans-serif;
  background-color: black;
  color: whitesmoke;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

